import React from 'react';
import Axios from "axios";
import User from "../User/User";

import './Users.scss';
import '../../../../Shared/Scss/buttons.scss';
import Button from "@material-ui/core/Button";
import detect from "detect.js";
import Loader from "../../../../Shared/icons/Loader/Loader";

export default class Users extends React.Component {
    constructor(props) {
        super(props);
        const maxMobileSize = 755;

        this.state = {
            nextPage: 1,
            usersPerPage: 6,
            users: [],
            fullUsersList: false,
            isLoading: false,
            longRequest: false,
            requestIsFail: false,
            buttonShowMoreContent: this.props.contentText.buttonShowMore,
        };

        const userAgent = detect.parse(navigator.userAgent);
        this.isNeedToFix = /Windows|Android/i.test(userAgent.os.family);
        this.isFirefox = /Firefox/i.test(userAgent.browser.family);

        this.correctUsersPerPageChecker = () => {
            if (document.body.clientWidth > maxMobileSize) {
                // this.setState({
                //     usersPerPage: 6,
                // });
                return 6;
            }

            // this.setState({
            //     usersPerPage: 3,
            // });
            return 3;
        };

        this.getUsers = (page, usersPerPage) => {
            if(this.state.fullUsersList || this.state.isLoading) return;
            this.setState({ isLoading: true });

            Axios.get(`https://frontend-test-assignment-api.abz.agency/api/v1/users?page=${page}&count=${usersPerPage}`).then(res => {
                const newUsers = res.data.users;
                const fullUsersList = res.data.page >= res.data.total_pages;
                this.setState((state) => {
                    const users = state.users.concat(newUsers);

                    return {
                        fullUsersList: fullUsersList,
                        nextPage: ++state.nextPage,
                        users: users,
                        isLoading: false,
                        longRequest: false
                    }
                });
            }).catch(() => {
                this.setState({requestIsFail: true});
            });
        };
    }

    componentDidMount() {
        const usersPerPage = this.correctUsersPerPageChecker();
        this.getUsers(this.state.nextPage, usersPerPage);

        //window.addEventListener("resize", this.correctUsersPerPageChecker);
    }

    render() {
        const contentText = this.props.contentText;
        let usersJSX = null;

        if (this.state.users.length > 0) {
            usersJSX = this.state.users.map((user, index) => {
                return <User key={index} idx={index} user={user} />
            });
        } else {
            usersJSX = [];

            for (let i = 0; i < this.correctUsersPerPageChecker(); i++) {
                usersJSX.push(<User key={i} idx={i} user={'placeholder'} />);
            }
        }

        return (
            <section className={`users${this.state.requestIsFail ? ' requestIsFail' : ''}`}>
                <div className="users__container">
                    <h2 className={`users__title heading2${this.isNeedToFix || this.isFirefox ? ' overpassFix' : ''}`}>{contentText.title}</h2>
                    <h5 className="users__subtitle heading5">{contentText.subtitle}</h5>
                    <div className="users__list">
                        {usersJSX}
                    </div>
                    <div onClick={() => {
                        this.getUsers(this.state.nextPage, this.correctUsersPerPageChecker());
                        setTimeout(() => {
                            if (this.state.isLoading) this.setState({longRequest: true})
                        }, 300);
                    }} className="users__button-show-more button">
                        <Button
                            variant="contained"
                            className={`button-item button-item_secondary${this.state.fullUsersList || this.state.isLoading ? ' disabled' : ''}`}
                            disabled={this.state.fullUsersList}
                        >
                            { this.state.longRequest ? <Loader /> : contentText.buttonShowMore }
                        </Button>
                    </div>
                </div>
            </section>
        )
    }
};