import React from "react";

import './User.scss';
import avatarPlaceholder from '../../../../Shared/images/avatarPlaceholder.png';
import detect from "detect.js";
import Blazy from 'blazy/blazy';
import Tooltip from "@material-ui/core/Tooltip";

export default class User extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            userImg: avatarPlaceholder,
            emailIsLong: false,
            user: this.props.user
        };

        const userAgent = detect.parse(navigator.userAgent);
        this.isWindows = /Windows/i.test(userAgent.os.family);
        this.isFirefox = /Firefox/i.test(userAgent.browser.family);
    }

    componentDidMount() {
        if (this.props.user === 'placeholder') return;
        this.userLoaded();
    }

    componentWillReceiveProps(nextProps, nextContext) {
        if (nextProps.user === 'placeholder') return;
        this.setState({user: nextProps.user}, this.userLoaded);
    }

    userLoaded() {
        const userImg = new Image();
        userImg.src = this.state.user.photo;
        userImg.onload = () => {
            this.setState({userImg: userImg.src});
        };

        const emailElem = document.querySelector(`.user.user${this.props.idx} .user__info-email`);
        if (emailElem.scrollWidth <= 209) return;

        this.setState({emailIsLong: true});
        emailElem.style.cursor = 'pointer';
    }

    render() {

        return (
            <div className={`user user${this.props.idx}`}>
                <img className="user__photo" src={this.state.userImg} alt="user"/>
                <div className="user__info">
                    <div
                        className={`user__info-name${this.isWindows || this.isFirefox ? ' overpassFixUserName' : ''}${this.props.user === 'placeholder' ? ' hide' : ''}`}
                        title={this.state.user.name}
                    >{this.state.user.name}</div>
                    <div className={`user__info-position${this.props.user === 'placeholder' ? ' hide' : ''}`}>{this.state.user.position}</div>
                    <Tooltip
                        title={this.state.user.email}
                        placement="bottom"
                        disableFocusListener={!this.state.emailIsLong}
                        disableHoverListener={!this.state.emailIsLong}
                        disableTouchListener={!this.state.emailIsLong}
                    >
                        <p className="user__info-email">{this.state.user.email}</p>
                    </Tooltip>
                    <div className="user__info-phone">{this.state.user.phone}</div>
                </div>

            </div>
        )
    }
}